<script setup>
import { ref, onMounted  } from 'vue';
import HelloWorld from '@/components/HelloWorld.vue';
import { supabase } from '../clients/supabase';
// import Swal from 'sweetalert2';
import axios from 'axios';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper-bundle.css';
import SwiperCore, { Pagination, Navigation } from 'swiper/core';

SwiperCore.use([Pagination, Navigation]);

const match1value = ref(""),
match2value = ref(""),
datevalue = ref(""),
locationvalue = ref(""),
news_headline = ref(""),
news_description = ref("");

function expandnav() {
  const nav = document.querySelector(".normalnav"),
  hiddennav = document.querySelector(".hiddennav")
  
  if (nav.style.height == "50%") {
    nav.style.height = "48px"
    hiddennav.style.display = "none"
  } else {
    nav.style.height = "50%"
    hiddennav.style.display = "block"
  }
}

async function seedatabase(matchNumber) {
  try {
    const response = await axios.post('./index.php', {
      [`getmatch${matchNumber}value`]: true
    });
    
    const matchValue = document.getElementById(`match${matchNumber}value`);
    
    const removewords = ["success", "error", "status", "error", "PHP", "Loaded", "!", "Connected", "fully", ",", "match1Message", "No", "message", "received", "Message", "received", "Getting", "match2Message", "match_1", "match_2", "successfully", "database", "dateMessage", "getdatevalue"];
    
    for (let i = 0; i < removewords.length; i++) {
      const wordRegex = new RegExp("\\b" + removewords[i] + "\\b", "g");
      response.data = response.data.replace(wordRegex, "");
    }
    
    response.data = response.data.replace(/['"]+/g, '');
    response.data = response.data.replace(/[:]+/g, '');
    response.data = response.data.replace(/[{}]+/g, '');
    response.data = response.data.replace(/[,]+/g, '');
    response.data = response.data.replace(/[!]+/g, '');
    
    matchValue.innerHTML = response.data;
    
    // console.log(response.data);
  } catch (error) {
    // console.error('Error:', error);
  }
}

async function see_date_database() {
  try {
    const response = await axios.post('./index.php', {
      getdatevalue: true
    });
    
    const dateValue = document.getElementById(`datevalue`);
    
    response.data = response.data.slice(-20);
    response.data = response.data.slice(0, 10) + " at " + response.data.slice(10);
    
    dateValue.innerHTML = response.data;
    
    // console.log(response.data);
  } catch (error) {
    console.error('Error:', error);
  }
}

async function see_location_database() {
  try {
    const response = await axios.post('./index.php', {
      getlocationvalue: true
    });
    
    const locationValue = document.getElementById(`locationvalue`);
    
    const removedwords = ["PHP", "Loaded", "Connected", "successfully", "Getting", "database", "location"]
    
    for (let i = 0; i < removedwords.length; i++) {
      const wordRegex = new RegExp("\\b" + removedwords[i] + "\\b", "g");
      response.data = response.data.replace(wordRegex, "");
    }
    
    response.data = response.data.replace(/[:]+/g, '');
    response.data = response.data.replace(/[!]+/g, '');
    
    locationValue.innerHTML = response.data;
    
    // console.log(response.data);
  } catch (error) {
    // console.error('Error:', error);
  }
}

async function see_news_database() {
  try {
    const response = await axios.post('./index.php', {
      getnewsvalue: true
    });
    
    const newsHeadline = document.getElementById(`news_headline`);
    const newsDescription = document.getElementById(`news_description`);
    const removedwords = ["PHP", "Loaded", "Connected", "successfully", "news"]
    
    for (let i = 0; i < removedwords.length; i++) {
      const wordRegex = new RegExp("\\b" + removedwords[i] + "\\b", "g");
      response.data = response.data.replace(wordRegex, "");
    }
    
    response.data = response.data.replace(/[:]+/g, '');
    response.data = response.data.replace(/[!]+/g, '');
    
    const splitResponse = response.data.split("-");
    newsHeadline.innerHTML = splitResponse[0];
    newsDescription.innerHTML = splitResponse[1];
    
    // console.log(response.data);
  } catch (error) {
    // console.error('Error:', error);
  }
}

onMounted(() => {
  seedatabase(1);
  seedatabase(2);
  see_date_database();
  see_location_database();
  see_news_database();
})

</script>

