<template>
  <div class="container bg-[#43425D] w-full h-full absolute text-white">
    
    <div class="bg-[#11111180] w-full absolute top-0 py-10 flex flex-col items-center text-center justify-center gap-4">
      <h1 class="font-bold text-2xl font-[roboto]">Admin editing page</h1>
      <ul class="max-w-4xl">
        <li>This website is hooked up to a secure database.</li>
        <li>Now that you are logged in using secure authentication, you can make changes to the database, which are extracted and displayed on the website.</li>
        <li>Once you have updated and added everything you want, return home and see the changes.</li>
      </ul>
      <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded mt-1 py-1" @click="returnhome">Return home</button>
    </div>
    
    <div class="w-full absolute bottom-0 flex items-center justify-center flex-col gap-10 pb-10">
      <section class="bg-black/50 rounded-md p-4 text-center">
        <h2 class="text-xl font-[roboto] font-bold mb-4">Next Match - Football team names</h2>
        
        <div class="flex gap-2 mb-2">
          <textarea id="match1" v-model="match1" cols="30" rows="1" placeholder="Team 1 name" class="p-2 text-black rounded-md resize-none"></textarea>
          <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded" @click="update(1)">Update</button>
        </div>
        
        <div class="flex gap-2 mb-2">
          <textarea id="match2" v-model="match2" cols="30" rows="1" placeholder="Team 2 name" class="p-2 text-black rounded-md resize-none mt-1"></textarea>
          <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded mt-1" @click="update(2)">Update</button>
        </div>
        
        <div class="flex gap-2 mb-2">
          <textarea id="date" v-model="date" cols="30" rows="1" placeholder="year:month:day 00:00:00" class="p-2 text-black rounded-md resize-none"></textarea>
          <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded mt-1" @click="updatedate">Update</button>
          <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded mt-1" @click="set_todays_date">Today's date</button>
        </div>
        
        <div class="flex gap-2 mb-2">
          <textarea id="location" v-model="location" cols="30" rows="1" placeholder="Location" class="p-2 text-black rounded-md resize-none mt-1"></textarea>
          <button class="bg-white hover:bg-[#ffffff95] duration-200 text-black font-semibold px-2 rounded mt-1" @click="updatelocation">Update</button>
        </div>
      </section>
      
      <section class="bg-black/50 rounded-md p-4 text-center overflow-y-scroll max-h-[300px] flex items-center justify-center flex-col latestnews">
        <h2 class="text-xl font-[roboto] font-bold mb-4">Latest news</h2>
        
        <div class="flex gap-2 mb-2">
          <textarea id="headline" v-model="headline" cols="30" rows="1" placeholder="Headline" class="p-2 text-black rounded-md resize-none overflow-y-scroll"></textarea>
        </div>
        
        <div class="flex gap-2 mb-2">
          <textarea id="description" v-model="description" cols="30" rows="1" placeholder="Description" class="p-2 text-black rounded-md resize-none mt-1 overflow-y-scroll"></textarea>
        </div>
        
        <button class="bg-white text-black duration-200 rounded font-semibold px-2 py-1 mt-2 hover:bg-[#ffffff95]" @click="add_to_news">Add news</button>
      </section>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import Container from '@/components/Container.vue';
import axios from 'axios';
import VueSweetalert2 from 'vue-sweetalert2';
import Swal from 'sweetalert2';
import { useRouter } from 'vue-router';
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
import { swal } from 'sweetalert2/dist/sweetalert2';

const date = ref(),
$router = useRouter(),
match1 = ref(''),
match2 = ref(''),
location = ref('');

function show_alert() {
  Swal.fire({
    position: 'top-right',
    title: 'Content updated',
    text: 'The content has been updated successfully',
    icon: 'success',
    showConfirmButton: false,
    timer: 1500
  });
}

async function update(number) {
  try {
    const response = await axios.post('./index.php', {
      [`match${number}Message`]: window['match' + number].value
    });
    
    show_alert();
  } catch (error) {
    // console.error('Error:', error);
  }
}

async function updatedate() {
  if (date.value === '') {
    return Swal.fire({
      title: 'Error',
      text: 'Please enter a date',
      icon: 'error',
      confirmButtonText: 'Ok'
    });
  }
  
  if (date.value.length !== 19) {
    return Swal.fire({
      title: 'Error',
      text: 'Please enter a valid date',
      icon: 'error',
      confirmButtonText: 'Ok'
    });
  }
  
  try {
    const response = await axios.post('./index.php', {
      dateMessage: date.value
    });
    
    show_alert();
  } catch (error) {
    console.error('Error:', error);
  }
}

async function updatelocation() {
  try {
    const response = await axios.post('./index.php', {
      locationMessage: location.value
    });
    
    show_alert();
  } catch (error) {
    console.error('Error:', error);
  }
}

function create_news() {
  const added_news = document.createElement('div');
  added_news.innerHTML = `
  <div class="bg-[#11111180] backdrop-blur-md p-4 border-2 rounded-lg mt-10 text-center">
    <h2 class="text-md font-[roboto] font-bold mb-4">Added news</h2>
    
    <div class="flex gap-2 mb-2">
      <p class="bg-white text-black w-full rounded-md px-2 font-semibold overflow-y-scroll">${window.headline.value}</p>
    </div>
    
    <div class="flex gap-2 mb-2">
      <p class="bg-white text-black w-full rounded-md px-2 font-semibold overflow-y-scroll">${window.description.value}</p>
    </div>
    
    <button class="text-white duration-200 rounded font-semibold px-2 py-1 mt-2 hover:bg-white hover:text-black" @click="remove_news">Remove</button>
  </div>
  `;
  
  // append to .latestnews
  document.querySelector('.latestnews').appendChild(added_news);
}

function add_to_news() {
  const headline = window.headline.value,
  description = window.description.value;
  
  if (headline === '' || description === '') {
    return Swal.fire({
      title: 'Error',
      text: 'Please enter a headline and description',
      icon: 'error',
      confirmButtonText: 'Ok'
    });
  }
  
  try {
    const response = axios.post('./index.php', {
      newsMessage: headline + ' - ' + description
    });
    
    show_alert();
    
    create_news();
    
  } catch (error) {
    console.error('Error:', error);
  }
}

function set_todays_date() {
  let today = new Date(),
  year = today.getFullYear(),
  month = today.getMonth() + 1,
  day = today.getDate(),
  hours = today.getHours(),
  minutes = today.getMinutes(),
  seconds = today.getSeconds();
  
  if (month < 10) {
    month = `0${month}`;
  }
  
  date.value = `${year}:${month}:${day} ${hours}:${minutes}:${seconds}`;
}

function returnhome() {
  $router.push({ name: "home" })
}
</script>

<style scoped lang="scss">
.latestnews {
  &::-webkit-scrollbar {
    width: 5px;
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: #ffffff95;
    border-radius: 5px;
  }
  
  &::-webkit-scrollbar-track {
    background-color: #ffffff20;
  }
}
</style>